import { useEffect } from 'react';

import styles from './useBodyBackground.module.scss';

const useBodyBackground = (url) => {
  useEffect(() => {
    const bdy = document.body;
    if (url) {
      bdy.classList.add(styles.root);
      bdy.style.backgroundImage = `url(${url})`;
    }
    return () => {
      bdy.classList.remove(styles.root);
      bdy.style.backgroundImage = null;
    };
  }, [url]);
};

export default useBodyBackground;
