import React, { useState, useCallback } from 'react';
import PropTypes from 'prop-types';
import useSound from 'use-sound';

import Button from '../../../components/Button';
import { withEpisode } from '../../../components/Episode/Episode.Context';

const SoundButton = (props) => {
  const {
    sound,
    url,
    onClick,

    /* eslint-disable react/prop-types */
    episodeSteps,
    assets,
    setStepProgress,
    getStepInfo,
    makeStep,
    setSeason,
    setEpisode,
    toggleAudio,
    setAudioVolume,
    /* eslint-enable react/prop-types */

    ...buttonProps
  } = props;
  const [loaded, setLoaded] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [play, { sound: stuff }] = useSound(assets[url], {
    sprite: {
      pew: [100, 75],
      pewpew: [100, 75],
      laser: [100, 75],
      boom: [100, 75],
    },
    onerror: () => { console.info('onerror'); },
    onend: () => { console.info('onend'); },
    onload: () => setLoaded(true),
  });
  const handleClick = useCallback((...args) => {
    play({ id: 'pew' });
    // if (typeof onClick !== 'function') return;
    return onClick(...args);
  });

  // console.info('stuff', props, stuff);

  if (!loaded) return null;

  return (
    <Button
      // eslint-disable-next-line react/jsx-props-no-spreading
      {...buttonProps}
      onClick={handleClick}
    />
  );
};

SoundButton.propTypes = {
  onClick: PropTypes.func.isRequired,
  url: PropTypes.string.isRequired,
  sound: PropTypes.string.isRequired,
};

export default SoundButton;

export const WithEpisode = withEpisode(SoundButton);
