import React from 'react';
import PropTypes from 'prop-types';
import Typist from 'react-typist';
import styles from './Slides.TypistSlide.module.scss';
import { withSlides } from './Slides.Context';
import Card from '../Card/Card';

const TypistSlide = ({
  className,
  children,
  nextSlide,
  nextDelay,
  startDelay,
}) => {
  const next = () => setTimeout(nextSlide, nextDelay);
  const [currentChild, setCurrentChild] = React.useState(0);

  const handleTypingDone = () => setCurrentChild((prev) => {
    if (prev >= children.length) {
      next();
      return prev;
    }
    return prev + 1;
  });

  let content = null;
  if (!Array.isArray(children)) {
    content = (
      <Typist
        startDelay={startDelay}
        onTypingDone={next}
        className={styles.slide__typist}
        // cursor={{
        //   // hideWhenDone: true,
        //   element: ' ',
        // }}
      >
        {children}
      </Typist>
    );
  } else {
    content = [
      ...children.slice(0, currentChild),
      (
        <Typist
          startDelay={currentChild === 0 ? startDelay : 0}
          key={`typist-${currentChild}`}
          className={styles.slide__typist}
          onTypingDone={handleTypingDone}
          // cursor={{
          //   // hideWhenDone: true,
          //   element: ' ',
          // }}
        >
          {children[currentChild]}
        </Typist>
      ),
    ];
  }

  const classNames = [
    className,
    styles.slideTypist,
  ];
  return (
    <div className={styles.slide}>
      <Card className={classNames.filter(Boolean).join(' ')}>
        {content}
      </Card>
    </div>
  );
};

TypistSlide.displayName = 'MKTypistSlide';

TypistSlide.propTypes = {
  startDelay: PropTypes.number,
  nextDelay: PropTypes.number,
  className: PropTypes.string,
  children: PropTypes.node,
  nextSlide: PropTypes.func.isRequired,
};

TypistSlide.defaultProps = {
  startDelay: 1000,
  nextDelay: 1000,
  className: null,
  children: null,
};

export default TypistSlide;

export const WithSlides = withSlides(TypistSlide);
