import React, { useEffect } from 'react';
import PropTypes from 'prop-types';

import styles from './Slides.Controls.module.scss';
import { withSlides } from './Slides.Context';
import { PrevButton, NextButton } from './Slides.ControlButton';

const Controls = withSlides(({
  slidesCount,
  currentSlide,
  setStepProgress,
}) => {
  useEffect(() => {
    setStepProgress(currentSlide / slidesCount);
  }, [currentSlide, slidesCount]);

  return (
    <div className={styles.root}>
      <div className={styles.buttonWrapper}>
        <PrevButton hide={currentSlide < 1} />
      </div>
      <div className={styles.progress}>
        {`${Math.min(currentSlide, slidesCount)} / ${slidesCount}`}
      </div>
      <div className={styles.buttonWrapper}>
        <NextButton hide={currentSlide > slidesCount} />
      </div>
    </div>
  );
});

Controls.propTypes = {
  setStepProgress: PropTypes.func.isRequired,
};

Controls.displayName = 'MKControls';

export default Controls;
