import React from 'react';
import PropTypes from 'prop-types';
import styles from './Card.module.scss';

// eslint-disable-next-line react/prop-types
const Svg = ({ className }) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    version="1.1"
    viewBox="0 0 100 100"
    height="100"
    width="100"
    className={className}
  >
    <g>
      <path
        d="M 95,5 H 10 v 60"
      />
      <path
        d="m 10,40 -10,-10 v -20 l 10,-10 h 45 l 5,5"
      />
    </g>
  </svg>
);

const Card = ({
  children,
  className,
  hide,
  appear,
}) => {
  const [show, setShow] = React.useState(!appear);

  React.useEffect(() => {
    if (appear && !show) setShow(true);
  });

  return (
    <div
      className={[
        className,
        styles.root,
        hide && styles.hide,
        appear && styles.rootAppear,
        appear && show && styles.rootShow,
      ].filter(Boolean).join(' ')}
    >
      <div className={styles.inner}>
        {children}
      </div>

      <Svg className={styles.cornerTopLeft} />

      <Svg className={styles.cornerBottomRight} />
    </div>
  );
};

Card.displayName = 'MKCard';

Card.propTypes = {
  className: PropTypes.string,
  children: PropTypes.node,
  appear: PropTypes.bool,
  hide: PropTypes.bool,
};

Card.defaultProps = {
  className: null,
  children: null,
  appear: null,
  hide: false,
};

export default Card;
